import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let Box = styled.div`
    padding-top: 30px;
`;
let Title = styled.h4`
    font-size: 25px;
    color: ${ props => props.color } 
`;


// class Detail2 extends React.Component {

//     componentDidMount() { //Detail2  Mount   ~

//     }
    
//     componentWillUnmount() { //Detail2  Unmount    ~

//     }
// }


function Detail(props) {

    let [alert, alertChange] = useState(true);
    let [inputData, inputDataChange] = useState('');

    useEffect(()=>{ // mount, update ,     

        axios.get() //Detail   &   

        let timer = setTimeout(()=>{ alertChange(false) }, 2000); //2  alert   
        console.log(' ');
        return ()=>{ clearTimeout(timer) } //<Detail>   
    },[]); //[]  ,  [alert]alert state  



    let { id } = useParams(); //:id   

    // - Detail.js   0    ~    
    //   0    ~  .
    //  shoes    .
    //  shoes   {id : 0}   .
    //   /:id      {} 
    //   .
    // find()  ES6  . Array      .
    // filter() ,     .

    // 1. find() array    ,   .
    // 2.    (  ) array     .
    // 3. return     .      .
    // 4.     URL /:id     (.id)    .

    //  /detail/0       id : 0  .
    // /detail/1     id : 1 .
    //     ,  HTML    .
    // !

    //        +  find()   
    //      id : 0  Ajax   .
    //   find()    ajax   
    // ajax   {}         .
    let findProduct = props.shoes.find(function(product){
        return product.id == id
    });

    let history = useHistory(); //   object

    return (
      <div className="container">
        <Box>
            <Title className="red"></Title>
        </Box>

        { inputData }
        <input onChange={(e)=>{ inputDataChange(e.target.value) }}/>

        {
            alert === true
            ? (<div className="my-alert2">
                    <p>   </p>
                </div>
              )
            : null
        }
        

        <div className="row">
          <div className="col-md-6">
            <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
          </div>
          <div className="col-md-6 mt-4">
            <h4 className="pt-5">{findProduct.title}</h4>
            <p>{findProduct.content}</p>
            <p>{findProduct.price}</p>

            <Info stock={props.stock}></Info>

            <button className="btn btn-danger" onClick={()=>{ props.stockChange([9,11,12]) }}></button> 
            <button className="btn btn-danger" onClick={()=>{
                history.push('/')
              }}></button> 
          </div>
        </div>
      </div>
    )
}

function Info(props){
    return (
        <p>: {props.stock[0]}</p>
    );
}

export default Detail;